import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			registerInfo:['用户名','密码','重复密码','电子邮件','激活码'],
			name:'',
			email:'',
			password:'',
			rePassword:'',
			yaoqiu:'',
			loginName:'',
			loginPassword:''
		}
	},
	methods:{
		ns(){
			console.log(11)
		},
		cancleMask(){
			this.$store.commit('handleMask',false)
		},
		handleColor(b){
			this.$store.commit('handleSign',b)
		},
		handel(){
			if(this.name == '' || this.email == '' || this.password == '' || this.rePassword == '' || this.yaoqiu == ''){
				uni.showToast({
					title:'请完整填入'
				})
				return
			}
			if(this.password !== this.rePassword){
				uni.showToast({
					title:'密码不一致'
				})
				return
			}
			if(this.yaoqiu != 123){
				uni.showToast({
					title:'请输入邀请码123'
				})
				return
			}
			this.put()
		},
		async put(fanhao) {
		    try {
				let obj1 = {
					id:this.name,
					email:this.email,
					password:this.password
				}
				let obj2 = {
					id:this.email,
					name:this.name,
					password:this.password
				}
				let str1 = JSON.stringify(obj1)
				let str2 = JSON.stringify(obj2)
				console.log(str1)
				console.log(str2)
		        let result1 = await client.put(`junzi/json/rl/${this.name}.json`, new Buffer(str1));
				let result2 = await client.put(`junzi/json/rl/${this.email}.json`, new Buffer(str2));
		        let duxie1 = await client.putACL(`junzi/json/rl/${this.name}.json`, 'public-read-write');
				let duxie2 = await client.putACL(`junzi/json/rl/${this.email}.json`, 'public-read-write');
				if(result1.res.status == 200 && result2.res.status == 200 && duxie1.res.status == 200 && duxie2.res.status == 200){
					alert('注册成功')
				}else{
					alert('注册失败')
				}
				this.name == ''
				this.email == ''
				this.password == ''
				this.rePassword == ''
				this.yaoqiu == ''
		        // console.log(result);
		    } catch (e) {
		        console.log(e);
				this.name == ''
				this.email == ''
				this.password == ''
				this.rePassword == ''
				this.yaoqiu == ''
		    }
		},
		login(){
			if(this.loginName == '' || this.loginPassword == ''){
				uni.showToast({
					title:'用户名或密码不能为空'
				})
				return
			}
			this.go()
		},
		go(){
			uni.request({
				url:`https://ns1997.oss-cn-hangzhou.aliyuncs.com/junzi/json/rl/${this.loginName}.json`
			}).then(res=>{
				console.log(res[1])
				if(res[1].statusCode == 200){
					if(res[1].data.password == this.loginPassword){
						this.$store.commit('handleLogin',true)
						this.$store.commit('handleMask',false)
						this.loginName = ''
						this.loginPassword = ''
						uni.showToast({
							title:'登陆成功'
						})
					}else{
						uni.showToast({
							title:'密码错误'
						})
					}
				}else{
					uni.showToast({
						title:'没有此用户'
					})
				}
			})
			.catch(err=>{
				uni.showToast({
					title:'请检查网络'
				})
			})
		}
	},
	computed:{
		...mapState(['sign','mask','nIslogin']),
		theSign(){
			return this.sign
		},
		theMask(){
			return this.mask
		},
		theLogin(){
			return this.nIslogin
		}
	}
}
